              {`Figure 2: Overall framework of CLASI. The diagram illustrates the flow of operations for speech translation. In Step 1, CLASI processes the current input audio data. Optionally, the retriever is activated to obtain the relevant information from External Knowledge. In this demonstration, using the translation pair “`}
              <span className="font-medium">伊辛模型:</span>
