const Title = ({ className, ...rest }: ComponentProps<'div'>) => (
  <div
    className={twMerge(
      'font-bold text-[#1D2129] text-[40px] sm:w-full xl:w-[800px] leading-[34px] text-center',
      'text-[32px]',
      className,
    )}
    {...rest}
  />
);

const Paragraph = ({ className, ...rest }: ComponentProps<'div'>) => (
  <div
    className={twJoin(
      'bg-[#FAFBFC]  sm:w-full xl:w-[1000px]rounded-[11px] leading-[20px] p-5 text-[#1D2129] text-[14px] font-normal',
      'px-6 py-10 rounded-none',
      className,
    )}
    {...rest}
  />
);

const Img = ({ className, ...rest }: ComponentProps<'img'>) => (
  <img className={twMerge('w-[900px] object-contain', className)} {...rest} />
);

const Display = ({
  list,
  className,
  onSlide,
  videoListRef,
  stopOther,
  loop = false,
}: {
  list: string[];
  videoListRef: React.MutableRefObject<unknown[]>;
  onSlide: () => void;
  stopOther: (src: string) => void;
  loop?: boolean;
} & ComponentProps<'div'>) => {
  const getSwiperStyle = () => {
    const width =
      window.innerWidth < 480
        ? window.innerWidth * 0.8
        : window.innerWidth * 0.4;
    const height = (width * 9) / 15.5;

    return { width, height };
  };
  return (
    <div className={twMerge('max-w-full overflow-hidden', className)}>
      <Swiper
        effect={'coverflow'}
        loop={loop}
        centeredSlides={true}
        slidesPerView={'auto'}
        modules={[Navigation, A11y, EffectCoverflow]}
        className="w-full overflow-hidden swiper-no-swiping"
        coverflowEffect={{
          rotate: 0,
          stretch: -100,
          depth: 200,
          modifier: 1,
          slideShadows: false,
        }}
        navigation={{
          nextEl: '.swiper-next',
          prevEl: '.swiper-prev',
        }}
        onSlideChange={onSlide}
        initialSlide={1}
      >
        {list.map((src, index) => (
          <SwiperSlide
            className="rounded-2xl overflow-hidden shadow"
            style={getSwiperStyle()}
            key={src}
          >
            <VideoPlayer
              onStart={stopOther}
              ref={node => videoListRef.current.push(node)}
              src={src}
              className="absolute inset-0 z-100 shadow"
            />
          </SwiperSlide>
        ))}
        <CoverLayer position="left" className="swiper-prev" />
        <CoverLayer position="right" className="swiper-next" />
      </Swiper>
    </div>
  );
};

const useVideoControl = () => {
  const videoListRef = useRef<unknown[]>([]);

  const stopAllVideos = () => {
    videoListRef.current.forEach(item => {
      (item as any)?.pause();
    });
  };

  const stopOtherExcept = (src: string) => {
    videoListRef.current
      .filter(item => item !== null)
      .forEach(ref => {
        if ((ref as any)?.src !== src) {
          (ref as any).pause();
        }
      });
  };

  return { videoListRef, stopAllVideos, stopOtherExcept };
};

const CoverLayer = ({
  onClick,
  className,
  position,
}: ComponentProps<'div'> & {
  position: 'left' | 'right';
}) => {
  return (
    <animated.div
      className={twMerge(
        'absolute top-1/2 z-50 cursor-pointer flex items-center justify-center bg-transparent w-[80px] -translate-y-1/2',
        className,
        position === 'left' ? 'left-0' : 'right-0',
      )}
    >
      <div
        onClick={onClick}
        className={twJoin(
          'justify-self-start flex justify-center items-center h-[64px] w-[64px] rounded-[32px]',
          'bg-[#F7F7F9] hover:opacity-80 shadow-md',
        )}
      >
        {position === 'left' ? <LeftIcon /> : <RightIcon />}
      </div>
    </animated.div>
  );
};
