import React, {
  ComponentProps,
  useEffect,
  useRef,
  useImperativeHandle,
} from 'react';
import Player, { Events } from 'xgplayer';
import 'xgplayer/dist/index.min.css';

type VideoPlayerProps = ComponentProps<'div'> & {
  src: string;
  onStart: (src: string) => void;
};
type RefProps = {
  pause: () => void;
};

const _VideoPlayer: React.ForwardRefRenderFunction<
  RefProps,
  VideoPlayerProps
> = (props: VideoPlayerProps, ref) => {
  const { src, className, onStart } = props;
  const VideoRef = useRef<HTMLDivElement>(null);
  const playerRef = useRef<unknown>(null);
  useEffect(() => {
    if (!VideoRef.current) {
      return;
    }
    playerRef.current = new Player({
      el: VideoRef.current,
      url: src,
      width: '100%',
      height: '100%',
      videoFillMode: 'fill',
      keyShortcut: false,
      commonStyle: {
        playedColor: 'rgba(0, 0, 255, 0.5)',
        sliderBtnStyle: {
          background: 'rgba(0, 0, 255, 0.3)',
        },
        volumeColor: 'rgba(0, 0, 255, 0.5)',
      },
    });

    (playerRef.current as Player).on(Events.PLAY, () => onStart(src));
  }, [src, onStart]);

  useImperativeHandle(
    ref,
    () => ({
      pause() {
        (playerRef.current as Player)?.pause();
      },
      src,
    }),
    [playerRef, src],
  );

  return (
    <div className={className}>
      <div ref={VideoRef} />
    </div>
  );
};
export const VideoPlayer = React.forwardRef(_VideoPlayer);
